<template>
	<div class="address-list-container">
		<topNav class="topNav" :title="title" :isleftarrow="isleftarrow"></topNav>
		<van-address-list
			v-model="chosenAddressId"
			:list="list"
			:switchable="isChoose"
			default-tag-text="默认"
			@add="onAdd"
			@edit="onEdit"
			@select="onSelect"/>
	</div>
</template>

<script>
	import util from '@/common/util/util'
	import topNav from '../../components/topNav.vue'
	import {
		mapMutations
	} from 'vuex'
	let flag;
	export default {
		data() {
			return {
				title: '',
				isleftarrow: '',
				chosenAddressId: '',
				list: [],
				disabledList: [],
				isChoose: false
			}
		},
		components: {
			topNav
		},
		created() {
			this.isChoose = this.$route.query.isChoose ? true : false;
			flag = false;
			this.getAddress();
		},
		mounted() {
			this.title = this.$route.meta.title;
			this.isleftarrow = this.$route.meta.isleftarrow;
		},
		methods: {
			...mapMutations(['updateDefaultAddress']),
			getAddress() {
				this.$request.get('/user/shipping-address/list', {
					token: util.storage.get('token')
				}).then(res => {
					// console.log(res);
					if (res.code !== 0) {
						return;
					}
					const list = res.data || [];
					// console.log(list);
					list.forEach(item => {
						// item.id= item.id
						item.name = item.linkMan
						item.tel = item.mobile
						if(item.isDefault === true){
							this.chosenAddressId = item.id;
						}
						// item.address = item.address
					})
					this.list = list;
					console.log(this.chosenAddressId);
				});
			},
			onAdd() {
				this.$toast('新增地址');
				this.$router.push({
					path: '/new-address'
				});
			},
			onEdit(item, index) {
				// console.log(item.id);
				this.$toast('编辑地址:' + index);
				this.$router.push({
					path: '/new-address',
					query: {
						id: item.id
					}
				});
			},
			onSelect(item) {
				if (!this.isChoose) return
				// ?会触发2次方法
				if (!flag) {
					flag = true;
					this.$request.post('/user/shipping-address/update',{
						id: item.id,
						token: util.storage.get('token'),
						linkMan: item.linkMan,
						mobile: item.mobile,
						isDefault: true,
						address:item.address,
						provinceId: item.provinceId,
						cityId: item.cityId,
						districtId: item.districtId,
						code: item.code
					}).then(res=>{
						console.log(res);
					});
					this.updateDefaultAddress(item);
					this.$router.back(-1);
				}
			}
		}
	}
</script>

